import React from 'react'

import Log from '../Log'
import i18n from '../i18n'

import DialogTitle from '@material-ui/core/DialogTitle'
import DialogContent from '@material-ui/core/DialogContent'
import DialogActions from '@material-ui/core/DialogActions'
import Paper from '@material-ui/core/Paper'

import Typography from '@material-ui/core/Typography'
import Button from '@material-ui/core/Button'
import IconButton from '@material-ui/core/IconButton'
import CloseIcon from '@material-ui/icons/Close'

import EnhancedTable from '../components/EnhancedTable'


/*
 * STYLES
 */
import { withStyles } from '@material-ui/core/styles'
const styles = theme => ({
	closeButton: {
		position: 'absolute',
		right: theme.spacing(1),
		top: theme.spacing(1),
		color: theme.palette.grey[500],
	},
	dialogContent: {
		backgroundColor: '#fafafa'
	},
});


const headCells = [
	{ id: 'time', type: 'Date', label: i18n.t('Time'), noWrap: true },
	{ id: 'module', label: i18n.t('Module'), noWrap: true },
	{ id: 'criticality', label: i18n.t('Criticality'), noWrap: true },
	{ id: 'message', label: i18n.t('Message') },
	{ id: 'stack', type: 'Object', label: i18n.t('Stack Trace') },
];

const rowStyles = { // https://material-ui.com/components/alert/#simple-alerts
	'criticality': {
		'debug': {
			color: 'rgba(0, 0, 0, 0.4)',
			backgroundColor: 'rgba(0, 0, 0, 0.1)'
		},
		'info': {
			color: 'rgb(13, 60, 97)',
			backgroundColor: 'rgb(232, 244, 253)'
		},
		'warn': {
			color: 'rgb(102, 60, 0)',
			backgroundColor: 'rgb(255, 244, 229)'
		},
		'error': {
			color: 'rgb(97, 26, 21)',
			backgroundColor: 'rgb(253, 236, 234)'
		},
		'success': {
			color: 'rgb(30, 70, 32)',
			backgroundColor: 'rgb(237, 247, 237)'
			
		}
	}
}

class LogsPage extends React.Component {
	
	render() {
		const { classes } = this.props;
		
		const logs = Log.getLog();
		return (


<React.Fragment>
	<DialogTitle disableTypography>
		<Typography variant="h6">Logs</Typography>
		<IconButton aria-label="close" className={classes.closeButton} onClick={() => this.props.onClose()}>
			<CloseIcon />
		</IconButton>
	</DialogTitle>
	<DialogContent className={classes.dialogContent}>
		
		<Paper className={classes.paper}>
			<EnhancedTable
				selectable={false}
				headCells={headCells}
				rows={logs}
				rowStyles={rowStyles}
				/>
		</Paper>
		
	</DialogContent>
	<DialogActions>
		<Button autoFocus onClick={() => this.props.onClose()} color="primary">
			{i18n.t('Close')}
		</Button>
	</DialogActions>
</React.Fragment>

		)
	}
}

export default withStyles(styles)(LogsPage);
